@import "../../variables";
.search-results {
  grid-area: list;
  padding: 3rem 0;
  display: flex;
  flex-direction: column;
}

.results {
  list-style: none;
  margin-bottom: 2rem;

  .preview {
    &__link {
      &:link,
      &:visited {
        display: flex;
        align-items: center;
        padding: 1.5rem 3.25rem;
        transition: all 0.3s;
        border-right: 1px solid #fff;
        text-decoration: none;
      }

      &:hover {
        background-color: $color-accent;
        transform: translateY(-2px);
        .preview__publisher {
            color: $color-primary;
          }
      }

      &--active {
        background-color: $color-txt;
      }
      
    }

    &__fig {
      flex: 0 0 5.8rem;
      border-radius: 50%;
      overflow: hidden;
      height: 5.8rem;
      margin-right: 2rem;
      position: relative;
      backface-visibility: hidden;

      &::before {
        content: "";
        display: block;
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-image: linear-gradient(to right bottom, $color-primary, $color-secondary);
        opacity: 0.4;
      }

      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: all 0.3s;
      }
    }

    &__data {
      display: grid;
      width: 100%;
      grid-template-columns: 1fr 2rem;
      row-gap: 0.1rem;
      align-items: center;
    }

    &__title {
      grid-column: 1/-1;
      font-size: 1.45rem;
      color: $color-primary;
      text-transform: uppercase;
      font-weight: 600;

      // This is how text is truncated!
      text-overflow: ellipsis;
      max-width: 25rem;
      white-space: nowrap;
      overflow: hidden;
    }

    &__publisher {
      font-size: 1.15rem;
      color: $color-accent;
      text-transform: uppercase;
      font-weight: 600;
    }

    &__user-generated {
      background-color: darken($color-txt, 2%);

      display: flex;
      align-items: center;
      justify-content: center;
      height: 2rem;
      width: 2rem;
      border-radius: 10rem;

      margin-left: auto;
      margin-right: 1.75rem;

      img {
        height: 1.2rem;
        width: 1.2rem;
      }
    }
  }
}

.pagination-wrapper{
  padding: 0 3.5rem;

  &__btn {
    &--prev {
      float: left;
    }
    &--next {
      float: right;
    }
  }
}
.btn--inline {
  color: $color-primary;
  font-size: 1.3rem;
  font-weight: 600;
  border: none;
  background-color: $color-accent;
  padding: 0.8rem 1.2rem;
  border-radius: 10rem;
  cursor: pointer;

  display: flex;
  align-items: center;
  transition: all 0.2s;
}