@import "./_variables.scss";
* {
    margin: 0;
    padding: 0;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }
  
  html {
    box-sizing: border-box;
    font-size: 62.5%;
  
    @media only screen and (max-width: $bp-medium) {
      font-size: 50%;
    }
  }
  
  body {
    font-family: "Nunito Sans", sans-serif;
    font-weight: 400;
    line-height: 1.6;
    color: $color-txt;
    background: $color-bg;
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 100vh
  }
  .container {
      max-width: 120rem;
      min-height: 117rem;
      margin: 4vw auto;
      background-color: #fff;
      border-radius: 9px;
      overflow: hidden;
      box-shadow: 0 2rem 6rem 0.5rem rgba($color-accent, 0.2);
    
      display: grid;
      grid-template-rows: 10rem minmax(100rem, auto);
      grid-template-columns: 1fr 2fr;
      grid-template-areas:
        'head head'
        'list recipe';
    
      @media only screen and (max-width: $bp-large) {
        max-width: 100%;
        margin: 0;
        border-radius: 0;
      }
    }